@import '../../index.scss';

.modal-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: transparent;
  backdrop-filter: blur(2px);
  top: 0;
  left: 0;

  .modal {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgba(46, 43, 43, 0.855);
    box-shadow: 0 0 10px ;
    border-radius: 0.25rem;
    min-width: 350px;
    width: 30%;
    top: 20%;
    padding: 1rem;
    overflow: hidden;


    button {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: transparent;
      border: 2px solid red;
      border-radius: 50%;
      padding: 0.6rem;
      height: 1rem;
      width: 1rem;
      color: red;
      top: 1rem;
      left: 1rem;
    }

    .loader {
      color: #fff;
      font-size: 40px;
      margin: 1rem;
    }
    .check {
      margin: 1rem;
    }
    .hollow-circle {
      position: absolute;
      top: 115px;
      transform-origin: center !important;
      transform-style: preserve-3d;
    }
    h1 {
      color: #fff;
      font-size: 40px;
      margin: 0;
      padding: 1rem;
      margin-top: 1rem;
    }
    .error {
      font-size: 40px;
      color: red;

    }
    p.error {
      font-size: 20px;
      width: 80%;
      text-align: justify;
    }
    
  }

}